body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App .MyBox {
    fill: purple;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.leftContainer {
  position: absolute;
  width: calc(100% - 350px);
  max-width: 750px;
  left: 20px;
}

@media only screen and (min-width: 1400px) {
  .rightContainer {
    width: 550px;
    position: absolute;
    left: 850px;
  }
  .barchartEmbed {
    width: 250px;
    float: left;
    margin: 10px;
  }
  .mapEmbed {
    width: 250px;
    float: left;
    margin: 10px;
  }
}


@media only screen and (min-width: 1200px) and (max-width: 1400px) {
  .rightContainer {
    width: 250px;
    position: absolute;
    left: 850px;
  }
}

@media only screen and (max-width: 1200px) {
  .rightContainer {
    width: 250px;
    position: absolute;
    right: 50px;
  }
}

@media only screen and (max-width: 775px) {
  .leftContainer {
    width: 100%;
    position: relative;
  }
  .rightContainer {
    width: 100%;
    max-width: 400px;
    position: relative;
    margin: 0 auto;
    right: 0;
  }
}

.barchart {
  display: inline-block;
}
.barchartEmbed {
  display: flex;
}

.mapEmbed {
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.chart-label {
  font-size: 1.5em;
  line-height: normal;
  color: black;
}

.chart-title {
  font-weight: bold;
}

.chart-body {
}

.percentage-label {
  font-style: italic;
}

.chart svg {
  overflow: visible !important;
}

.chart.large {
  height: 400px;
}

.y-axis-label {
  white-space: nowrap;
}

.y-label {
  white-space: nowrap;
}

d3fc-svg svg {
  overflow: visible !important;
}

.annotation-line > line {
  fill-opacity: 0;
  stroke: #999;
  stroke-width: 1;
  stroke-dasharray: 3, 3;
}

.tooltip {	
  position: absolute;			
  text-align: center;				
  display: inline-block;		
  padding: 4px;				
  font: 12px sans-serif;		
  background: white;	
  display: inline-block;
  border: 0px;		
  border-radius: 3px;			
  pointer-events: none;			
}

.grid {
  color: grey;
}
input {
    outline: none;
}
.embedcode {
    display: block;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    line-height: 1.42857;
    word-break: break-all;
    word-wrap: break-word;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
}
text {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.region {

}
.loading-spinner {
    position: absolute;
    left: calc(50% - 50px);
    top: calc(50% - 50px);
}
.map-navbar {
    position: absolute;
    width : 200px;
    left: 0;
    top: 0;
    color: #8d97ad;
    font-size: 1em;
    letter-spacing: 2px;
    line-height: 22px;
    height: 100%;
    overflow-y: scroll;
}

.map-navbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.map-navbar::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

.map-navbar::-webkit-scrollbar-thumb
{
	background-color: #000000;
}

